.search {
  display: flex;
  position: relative;
  flex-grow: 1;
  z-index: 2;
  margin-right: 24px;
}
.search__back {
  display: none;
}
.search__btn {
  display: none;
}
.search__btn .svg-icon {
  display: block;
}
.search__btn .svg-icon use {
  fill: transparent;
  stroke: #131417;
}
.search__icon {
  width: 24px;
  height: 24px;
  position: absolute;
  left: 16px;
  display: flex;
  align-items: center;
  justify-content: center;
}
.search__form {
  display: flex;
  align-items: center;
  position: relative;
  flex-grow: 1;
}
.search__wrap {
  position: relative;
  flex-grow: 1;
}
.search__input {
  width: 100%;
  color: #5A5A5A;
  font-family: Inter;
  font-size: 16px;
  font-style: normal;
  font-weight: 400;
  line-height: 150%;
  padding: 11px 16px 11px 48px;
  border: 1px solid #73AFF4;
  background: #FFF;
  border-radius: 10px;
}
.search__input::-webkit-input-placeholder {
  color: #5A5A5A;
  font-family: Inter;
}
.search__input::-moz-placeholder {
  color: #5A5A5A;
  font-family: Inter;
}
.search__input:-moz-placeholder {
  color: #5A5A5A;
  font-family: Inter;
}
.search__input:-ms-input-placeholder {
  color: #5A5A5A;
  font-family: Inter;
}
@media (max-width: 1300px) {
  .search__input {
    font-size: 14px;
    padding: 12px 16px 13px 37px;
  }
  .search__icon {
    left: 10px;
  }
}
@media (max-width: 960px) {
  .search {
    margin-right: 0;
    margin-bottom: 12px;
  }
  .search__wrap {
    display: flex;
    align-items: center;
  }
  .search__delete {
    display: flex;
    align-items: center;
    margin-left: 10px;
    flex: none;
    opacity: 0.30000001192092896;
  }
  .search__back {
    display: flex;
    align-items: center;
    margin-right: 16px;
    flex: none;
    margin-bottom: 6px;
  }
  .search__form {
    padding-bottom: 6px;
    border-bottom: 1px solid #D5D5D5;
  }
  .search__form-block {
    flex-grow: 1;
    display: flex;
    align-items: center;
  }
  .search__input {
    width: 100%;
    color: #333;
    font-family: PT Sans;
    font-size: 16px;
    font-style: normal;
    font-weight: 400;
    line-height: 14px;
    padding: 0;
    height: 38px;
    border: 0;
    background: #FFF;
    border-radius: 0;
  }
  .search__input::-webkit-input-placeholder {
    color: #333;
    font-family: PT Sans;
  }
  .search__input::-moz-placeholder {
    color: #333;
    font-family: PT Sans;
  }
  .search__input:-moz-placeholder {
    color: #333;
    font-family: PT Sans;
  }
  .search__input:-ms-input-placeholder {
    color: #333;
    font-family: PT Sans;
  }
  .search__submit.btn {
    padding: 12px 16px;
    border-radius: 5px;
    font-size: 16px;
    line-height: 14px;
    margin-left: 16px;
    flex: none;
  }
}
