.btn {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  text-align: center;
  font-family: Inter;
  font-style: normal;
  font-weight: 500;
  font-size: 14px;
  line-height: 14px;
  padding: 16px 23px;
  color: #000;
  background-color: transparent;
  text-decoration: none;
  border-radius: 8px;
  border: 1px solid #73AFF4;
  transition: background-color 0.3s, border-color 0.3s, box-shadow 0.3s, color 0.3s ease;
}
.btn--blue {
  color: #7397F5;
}
.btn--fullblue {
  color: #fff;
  background-color: #7397F5;
}
.btn--grey {
  border-color: #AAA;
  color: #AAA;
}
.btn--long {
  display: flex;
  width: 100%;
}
.btn--bold {
  font-weight: 700;
}
.btn--pt {
  font-family: 'PT Sans', sans-serif;
}
.btn--middle {
  border-radius: 4px;
  padding: 10px 15px;
}
