  }
  .btn--blue:hover {
    background-color: #7397F5;
    color: #fff;
  }
  .btn--blue:hover .svg-icon use {
    stroke: #fff;
  }
  .btn--grey:hover {
    background-color: #000;
    border-color: #000;
    color: #fff;
  }
  .btn--grey:hover .svg-icon use {
    stroke: #fff;
  }
  .btn--red:hover {
    background-color: #000;
    border-color: #000;
    color: #fff;
  }
  .btn--red:hover .svg-icon use {
    stroke: #fff;
  }
  .btn--orange:hover {
    background-color: #000;
    border-color: #000;
    color: #fff;
  }
  .btn--orange:hover .svg-icon use {
    stroke: #fff;
  }
  .btn--default:hover {
    color: #fff;
    background-color: #FF3868;
  }
  .btn--default:hover .svg-icon use {
    stroke: #fff;
  }
  .btn--default:focus {
