  display: flex;
  flex-direction: column;
  position: relative;
  max-height: 180px;
}
.check .mCS-dark.mCSB_scrollTools .mCSB_dragger .mCSB_dragger_bar {
  border-radius: 0;
  background-color: #CBCBCB !important;
  border-radius: 10px;
  opacity: 1 !important;
}
.check .mCS-dark.mCSB_scrollTools .mCSB_draggerRail {
  background-color: transparent !important;
}
.check .mCustomScrollBox {
  padding-right: 16px;
}
.check .mCSB_scrollTools {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  width: 3px;
}
.check .mCSB_inside > .mCSB_container {
  margin-right: 0;
}
.check__main {
  display: inline-flex;
  align-items: center;
}
.check__item {
  display: flex;
  justify-content: space-between;
  position: relative;
  align-items: center;
  margin-bottom: 12px;
}
.check__item:hover .check__control {
  border-color: #131417;
}
.check__item:last-child {
  margin-bottom: 0;
}
.check input {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 2;
  cursor: pointer;
  opacity: 0;
}
.check input:checked ~ .check__main .check__control {
  border-color: #7397F5;
  background-color: #7397F5;
}
.check input:checked ~ .check__main .check__control .svg-icon {
  opacity: 1;
}
.check:last-child {
  margin-bottom: 0;
}
.check__control {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 20px;
  height: 20px;
  border-radius: 3px;
  margin-right: 12px;
  flex: none;
  border: 1px solid #D5D5D5;
  transition: border-color 0.3s, background-color 0.3s ease;
}
.check__control .svg-icon {
  display: block;
  opacity: 0;
  margin-left: 1px;
  transition: opacity 0.3s ease;
}
.check__count {
  color: #AAA;
  font-family: 'PT Sans', sans-serif;
  font-size: 12px;
  font-style: normal;
  font-weight: 400;
  line-height: 14px;
}
.check__name {
  font-family: 'PT Sans', sans-serif;
  color: #333;
  font-size: 14px;
  font-weight: 400;
  line-height: 16px;
}
.item {
  width: calc(25% - 24px);
  margin: 0 12px 24px;
}
.item:hover .item__name {
  color: #125BAE;
}
.item:hover .item__img-main {
  transform: scale(1.2);
}
.item__type {
  color: #AAA;
  font-size: 14px;
  margin-bottom: 4px;
  font-family: 'PT Sans', sans-serif;
  line-height: 16px;
}
.item__list {
  display: flex;
  flex-wrap: wrap;
  margin: 0 -12px -24px;
}
.item__name {
  display: inline-flex;
  align-items: center;
  color: #333;
  font-size: 14px;
  margin-bottom: 16px;
  font-family: 'PT Sans', sans-serif;
  line-height: 16px;
}
.item__info {
  padding: 450px 11px 20px;
  position: absolute;
  top: 0;
  left: -11px;
  right: -11px;
  background-color: #fff;
  box-shadow: 0px 4px 20px rgba(109, 109, 109, 0.12);
  border-radius: 8px;
  opacity: 0;
  pointer-events: none;
  transition: opacity 0.3s ease;
}
.item__btn {
  margin-top: 16px;
}
.item__btn--not {
  margin-top: 46px;
}
.item__img {
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  height: 200px;
  margin-bottom: 16px;
  background-color: #F8F8FA;
  overflow: hidden;
}
.item__img-main {
  max-height: 100%;
  max-width: 100%;
  transition: transform 0.3s ease;
}
.item__price {
  display: flex;
  align-items: center;
  color: #333;
  font-size: 16px;
  font-family: 'PT Sans', sans-serif;
  font-weight: 700;
  line-height: 14px;
}
.item__price .old {
  display: inline-flex;
  font-family: 'PT Sans', sans-serif;
  font-size: 12px;
  line-height: 14px;
  margin-left: 8px;
  text-decoration: line-through;
  color: #AAA;
}
.item__stock {
  position: absolute;
  left: 12px;
  bottom: 12px;
  font-family: 'PT Sans', sans-serif;
  font-weight: 700;
  line-height: 14px;
  color: #fff;
  padding: 6px 10px;
  background-color: #7397F5;
  border-radius: 4px;
}
.item__status {
  position: absolute;
  display: flex;
  align-items: center;
  top: 12px;
  left: 12px;
  font-family: 'PT Sans', sans-serif;
  font-size: 14px;
  font-style: normal;
  font-weight: 400;
  line-height: 16px;
  padding: 8px;
  background-color: #FFF;
  border-radius: 4px;
  border: 1px solid #F2F2F2;
}
.item__status img {
  margin-left: 4px;
}
@media (max-width: 1300px) {
  .item {
    width: calc(33% - 24px);
  }
}
@media (max-width: 960px) {
  .item {
    width: calc(33% - 12px);
    margin-left: 6px;
    margin-right: 6px;
  }
  .item__list {
    margin-left: -6px;
    margin-right: -6px;
  }
}
@media (max-width: 960px) {
  .item {
    width: calc(50% - 12px);
  }
}
.range {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.range__item {
  display: flex;
  align-items: center;
  position: relative;
  padding: 10px 8px;
  padding-left: 26px;
  border-radius: 4px;
  border: 1px solid #D5D5D5;
  background: #FFF;
  height: 36px;
  width: calc(50% - 21px);
}
.range__item span {
  position: absolute;
  left: 8px;
  color: #AAA;
  font-size: 12px;
  line-height: 14px;
}
.range__delimiter {
  width: 10px;
  height: 1px;
  flex: none;
  margin: 0 16px;
  background-color: #D5D5D5;
}
.range input {
  background-color: transparent;
  font-family: PT Sans;
  font-size: 14px;
  font-style: normal;
  font-weight: 400;
  line-height: 16px;
  width: 100%;
  border: 0;
  height: 100%;
}
.range .ui-slider {
  margin-top: 27px;
  height: 1px;
  background: #CBCBCB;
  border: 0;
}
.range .ui-slider-range {
  height: 1px;
  background: #131417;
  border: 0;
  border-radius: 0 !important;
}
.range .ui-slider-horizontal .ui-slider-handle {
  margin-left: -12px;
}
.range .ui-slider-handle {
  top: -12px;
  border: 1px solid #131417;
  background-color: #fefefd;
  width: 24px;
  height: 24px;
  border-radius: 50%;
  display: inline-block;
}
@media (max-width: 1200px) {
  .range {
    width: 100%;
  }
}
h2,
.h2 {
  font-family: PT Sans;
  font-size: 22px;
  font-style: normal;
  font-weight: 400;
  line-height: 28px;
  color: #333;
  margin: 0;
}
